.mypage-container {
  max-width: 720px;
  margin: 24px auto;
  padding: 20px;
  border-radius: 16px;
  background: #f7f8fb;
}

.mypage-header {
  background: #0b0d10;
  color: #fff;
  padding: 18px;
  border-radius: 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.15);
}

.mypage-header-left {
  display: flex;
  align-items: center;
  gap: 14px;
}

.mypage-avatar {
  width: 72px;
  height: 72px;
  border-radius: 18px;
  background: #1b1f24;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.mypage-avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.mypage-avatar i {
  font-size: 2.2rem;
  color: #9aa1ab;
}

.mypage-kicker {
  margin: 0;
  font-size: 0.75rem;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.6);
}

.mypage-header-info h2 {
  margin: 6px 0 4px;
  font-size: 1.4rem;
  color: #fff;
  text-align: left;
}

.mypage-subtitle {
  margin: 0 0 6px;
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.85rem;
}

.mypage-meta {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  font-size: 0.78rem;
  color: rgba(255, 255, 255, 0.6);
}

.icon-button {
  border: 1px solid rgba(255, 255, 255, 0.2);
  background: transparent;
  color: #fff;
  border-radius: 10px;
  width: 40px;
  height: 40px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.mypage-section {
  margin-top: 16px;
  background: #fff;
  border: 1px solid #e6e9f0;
  border-radius: 16px;
  padding: 18px;
  box-shadow: 0 8px 18px rgba(15, 23, 42, 0.06);
}

.mypage-section-title {
  margin: 0 0 12px;
  font-size: 1rem;
  color: #111827;
}

.mypage-menu {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  gap: 10px;
}

.mypage-menu-item {
  background: #f8f9fc;
  border-radius: 14px;
  padding: 14px;
  display: flex;
  align-items: center;
  gap: 10px;
  color: #121826;
  text-decoration: none;
  border: 1px solid #e6e9f0;
}

.mypage-menu-item i {
  font-size: 1.2rem;
  color: #0d6efd;
}

.mypage-list-menu {
  display: grid;
  gap: 4px;
}

.mypage-list-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 4px;
  border-bottom: 1px solid #eef1f4;
  text-decoration: none;
  color: #121826;
  font-size: 0.95rem;
}

.mypage-list-item i {
  color: #adb5bd;
}

.mypage-section-head {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
  padding-bottom: 12px;
  margin-bottom: 14px;
  border-bottom: 1px solid #eef1f4;
}

.back-link {
  color: #0d6efd;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  gap: 4px;
  font-weight: 600;
}

.mypage-section-title {
  font-weight: 700;
  letter-spacing: -0.01em;
}

.mypage-form input[type="text"] {
  background: #f9fafc;
}

.mypage-primary,
.mypage-secondary,
.mypage-danger-btn {
  box-shadow: 0 6px 14px rgba(13, 110, 253, 0.12);
}

.mypage-secondary {
  background: #f8f9fc;
}

.mypage-toggle-item,
.mypage-stat,
.mypage-list li {
  box-shadow: 0 4px 12px rgba(15, 23, 42, 0.04);
}

.mypage-empty {
  background: #f8f9fc;
  border-radius: 12px;
  border: 1px dashed #d7dce6;
}

.mypage-form {
  display: grid;
  gap: 12px;
}

.mypage-label {
  font-size: 0.85rem;
  color: #6c757d;
}

.mypage-form input[type="text"] {
  border: 1px solid #ced4da;
  border-radius: 10px;
  padding: 10px 12px;
}

.mypage-primary {
  border: none;
  border-radius: 10px;
  padding: 10px 14px;
  background: #0d6efd;
  color: #fff;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  gap: 8px;
  justify-content: center;
}

.mypage-toggle-list {
  display: grid;
  gap: 12px;
}

.mypage-toggle-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
  background: #f7f8fb;
  padding: 12px 14px;
  border-radius: 12px;
  border: 1px solid #e9ecf3;
}

.mypage-toggle-item p {
  margin: 6px 0 0;
  color: #6c757d;
  font-size: 0.82rem;
}

.switch {
  position: relative;
  display: inline-block;
  width: 44px;
  height: 24px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #cfd4dd;
  transition: 0.2s;
  border-radius: 999px;
}

.slider:before {
  position: absolute;
  content: "";
  height: 18px;
  width: 18px;
  left: 3px;
  bottom: 3px;
  background-color: white;
  transition: 0.2s;
  border-radius: 50%;
}

.switch input:checked + .slider {
  background-color: #0d6efd;
}

.switch input:checked + .slider:before {
  transform: translateX(20px);
}

.mypage-note {
  margin-top: 12px;
  font-size: 0.82rem;
  color: #6c757d;
}

.mypage-stats {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(110px, 1fr));
  gap: 10px;
}

.mypage-stat {
  background: #f7f8fb;
  border-radius: 14px;
  padding: 14px;
  border: 1px solid #e9ecf3;
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.stat-label {
  font-size: 0.8rem;
  color: #6c757d;
}

.stat-value {
  font-size: 1.3rem;
  color: #0b1f38;
}

.mypage-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: grid;
  gap: 10px;
}

.mypage-list li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px;
  background: #f7f8fb;
  border-radius: 12px;
  border: 1px solid #e9ecf3;
  font-size: 0.9rem;
}

.mypage-link {
  color: #0b1f38;
  font-weight: 600;
  text-decoration: none;
}

.mypage-empty {
  color: #6c757d;
  text-align: center;
  padding: 18px 0;
}

.mypage-actions {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
}

.mypage-secondary {
  border: 1px solid #d7dce6;
  background: #fff;
  color: #0b1f38;
  padding: 10px 14px;
  border-radius: 10px;
  display: inline-flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
}

.mypage-danger {
  margin-top: 16px;
  padding: 14px;
  border-radius: 12px;
  border: 1px solid #ffd7d7;
  background: #fff5f5;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.mypage-danger p {
  margin: 0;
  color: #b42318;
  font-size: 0.85rem;
}

.mypage-danger-btn {
  border: none;
  border-radius: 10px;
  padding: 10px 14px;
  background: #dc3545;
  color: #fff;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  gap: 8px;
  justify-content: center;
}

.file-upload-wrapper_mypage {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.file-upload-input_mypage {
  display: none;
}

.file-upload-label_mypage {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 10px 14px;
  border-radius: 10px;
  background: #0d6efd;
  color: #fff;
  cursor: pointer;
  width: fit-content;
}

.file-upload-filename_mypage {
  font-size: 0.82rem;
  color: #6c757d;
}

@media (max-width: 600px) {
  .mypage-container {
    margin: 16px;
    padding: 16px;
  }

  .mypage-menu {
    grid-template-columns: 1fr;
  }

  .mypage-list li {
    flex-direction: column;
    align-items: flex-start;
    gap: 6px;
  }

  .mypage-actions {
    flex-direction: column;
  }
}
