.search-page-container {
  max-width: 760px;
  height: 100%;
  margin: 30px auto;
  padding: 24px;
  font-family: "Pretendard", sans-serif;
  background: #ffffff;
  border-radius: 16px;
  box-shadow: 0 12px 30px rgba(15, 23, 42, 0.08);
  border: 1px solid rgba(11, 61, 145, 0.12);
}

.search-hero {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 16px;
  margin-bottom: 16px;
}

.search-eyebrow {
  font-size: 0.75rem;
  font-weight: 700;
  letter-spacing: 0.3em;
  text-transform: uppercase;
  color: #f2a94b;
  margin-bottom: 6px;
}

.search-title {
  font-size: 1.8rem;
  margin: 0 0 6px;
  color: #0b1322;
}

.search-subtitle {
  color: #5b6678;
  margin: 0;
}

.search-meta {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
}

.search-meta span {
  background: #f2f5fb;
  border-radius: 999px;
  padding: 6px 10px;
  font-size: 0.85rem;
  color: #0b1322;
  font-weight: 600;
}

.search-input-wrap {
  position: relative;
  margin-bottom: 16px;
}

.search-input-wrap i {
  position: absolute;
  left: 14px;
  top: 50%;
  transform: translateY(-50%);
  color: #0b3d91;
  font-size: 1.1rem;
}

.search-input {
  width: 100%;
  padding: 12px 44px;
  border-radius: 12px;
  border: 1px solid #d8e1f1;
  font-size: 1rem;
  transition: all 0.2s ease-in-out;
  background: #f9fbff;
}

.search-input:focus {
  outline: none;
  border-color: #0b3d91;
  box-shadow: 0 0 0 3px rgba(11, 61, 145, 0.12);
}

.clear-btn {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  border: none;
  background: transparent;
  color: #8a93a3;
  cursor: pointer;
}

.search-filters {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
  margin-bottom: 16px;
}

.search-filters button {
  border: 1px solid #d8e1f1;
  background: #ffffff;
  border-radius: 999px;
  padding: 6px 14px;
  font-weight: 600;
  color: #0b1322;
  cursor: pointer;
}

.search-filters button.active {
  background: #0b3d91;
  color: #ffffff;
  border-color: #0b3d91;
}

.search-hint,
.search-empty {
  padding: 18px;
  border-radius: 12px;
  background: #f7f8fc;
  color: #5b6678;
  text-align: center;
}

.search-empty i {
  font-size: 1.6rem;
  color: #0b3d91;
  margin-bottom: 8px;
}

.search-results {
  list-style: none;
  padding: 0;
  margin: 0;
}

.search-results.grouped {
  display: grid;
  gap: 18px;
}

.result-group h3 {
  margin: 0 0 8px;
  color: #0b1322;
  font-size: 1rem;
}

.result-group ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: grid;
  gap: 8px;
}

.search-item {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 10px 12px;
  border-radius: 12px;
  transition: all 0.2s ease;
  cursor: pointer;
  background: #ffffff;
  border: 1px solid rgba(11, 61, 145, 0.1);
}

.search-item:hover {
  background: #eef3ff;
  transform: translateY(-2px);
}

.search-item i {
  font-size: 1.05rem;
  color: #0b3d91;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background: rgba(11, 61, 145, 0.12);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  flex: 0 0 36px;
}

.result-link {
  text-decoration: none;
  color: #0b1322;
  font-weight: 600;
  flex: 1;
}

.result-link:hover {
  text-decoration: underline;
}

@media screen and (max-width: 600px) {
  .search-page-container {
    margin: 16px;
    padding: 18px;
  }

  .search-hero {
    flex-direction: column;
  }
}
