.signup-container {
  --ink: #2b2b2b;
  --muted: #8b8f96;
  --accent: #fee500;
  --surface: #f7f6f2;
  --field: #ffffff;
  --edge: #e6e2d8;
  width: min(380px, 100%);
  margin: 36px auto;
  padding: 28px 24px 24px;
  border-radius: 18px;
  background: var(--surface);
  border: 1px solid var(--edge);
  position: relative;
  overflow: hidden;
  box-sizing: border-box;
}

.signup-container::before {
  content: "";
  position: absolute;
  width: 240px;
  height: 240px;
  right: -140px;
  top: -140px;
  background: linear-gradient(135deg, rgba(255, 170, 170, 0.35), rgba(255, 225, 225, 0));
  pointer-events: none;
}

.auth-hero {
  margin-top: 8px;
  margin-bottom: 20px;
}

.auth-hero h2 {
  text-align: left;
  font-size: 1.55rem;
  font-weight: 800;
  color: var(--ink);
  margin: 0 0 10px;
  letter-spacing: -0.02em;
  line-height: 1.35;
}

.auth-sub {
  margin: 0;
  font-size: 0.84rem;
  color: var(--muted);
  line-height: 1.5;
}

.social-stack {
  display: grid;
  gap: 10px;
  margin-bottom: 12px;
}

.social-login {
  display: flex;
  align-items: center;
  gap: 12px;
  width: 100%;
  border-radius: 12px;
  padding: 12px 14px;
  border: 1px solid #e7e7e7;
  background: #ffffff;
  cursor: pointer;
  transition: transform 0.2s ease, filter 0.2s ease;
}

.social-login:hover {
  transform: translateY(-1px);
  filter: brightness(0.99);
}

.social-login img {
  width: 34px;
  height: 34px;
  object-fit: contain;
}

.social-login.naver {
  background: #03c75a;
  border-color: #03c75a;
}

.social-login.naver .social-title,
.social-login.naver .social-sub {
  color: #ffffff;
}

.social-texts {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.social-title {
  font-size: 0.95rem;
  font-weight: 700;
  color: var(--ink);
}

.social-sub {
  font-size: 0.78rem;
  color: var(--muted);
}

.other-signup-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 44px;
  border: none;
  border-radius: 12px;
  background: #e9e8e4;
  color: #6f7379;
  font-size: 0.92rem;
  font-weight: 700;
  cursor: pointer;
  transition: filter 0.2s ease;
  text-decoration: none;
  margin-bottom: 14px;
}

.other-signup-button:hover {
  filter: brightness(0.98);
}

.step {
  opacity: 0;
  transform: translateY(12px);
  transition: all 0.3s ease;
  margin-bottom: 1rem;
}

.step.active {
  opacity: 1;
  transform: translateY(0);
}

.Input_Group_S {
  position: relative;
  margin-bottom: 16px;
  width: 100%;
}

.Input_Group_S input {
  width: 100%;
  height: 46px;
  padding: 12px 12px 12px 64px;
  border: 1px solid var(--edge);
  border-radius: 12px;
  background: var(--field);
  font-size: 15px;
  color: var(--ink);
  transition: border-color 0.2s ease;
  outline: none;
  box-sizing: border-box;
}

.Input_Group_S input:focus {
  border-color: #2b2b2b;
}

.Input_Group_S i {
  position: absolute;
  top: 44%;
  left: 14px;
  transform: translateY(-50%);
  font-size: 24px;
  color: #a6aab1;
  pointer-events: none;
  transition: color 0.2s ease;
  z-index: 1;
}

.Input_Group_S input:focus ~ i {
  color: #2b2b2b;
}

.Input_Group_S label {
  position: absolute;
  left: 44px;
  top: 40%;
  transform: translateY(-50%);
  background: transparent;
  font-size: 22px;
  color: #a0a4aa;
  padding: 0 6px;
  pointer-events: none;
  transition: all 0.2s ease;
}

.Input_Group_S input:focus + label,
.Input_Group_S input:not(:placeholder-shown) + label {
  top: -8px;
  left: 58px;
  font-size: 11px;
  background: var(--surface);
  color: #2b2b2b;
  font-weight: 600;
}

.file-upload-wrapper {
  margin: 1.2rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.file-upload-label {
  padding: 12px 24px;
  border-radius: 12px;
  background: #2b2b2b;
  color: #fff;
  cursor: pointer;
  font-size: 0.92rem;
  font-weight: 700;
  transition: transform 0.2s ease, filter 0.2s ease;
}

.file-upload-label:hover {
  transform: translateY(-1px);
  filter: brightness(1.03);
}

.file-upload-filename {
  margin-top: 10px;
  font-size: 0.85rem;
  color: var(--muted);
}

.terms {
  display: flex;
  align-items: center;
  gap: 6px;
  margin: 10px 0 14px;
  font-size: 0.85rem;
  color: var(--muted);
}

.terms input[type="checkbox"] {
  width: 16px;
  height: 16px;
  accent-color: #2b2b2b;
}

.terms a {
  color: #2b2b2b;
  text-decoration: underline;
  font-weight: 600;
}

.signup-container button[type="submit"] {
  width: 100%;
  height: 48px;
  border: none;
  border-radius: 12px;
  color: #ffffff;
  font-size: 1rem;
  font-weight: 700;
  cursor: pointer;
  transition: transform 0.2s ease, filter 0.2s ease;
}

.signup-container button[type="submit"]:hover {
  transform: translateY(-1px);
  filter: brightness(0.98);
}

.terms-checkbox {
  margin-bottom: 14px;
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 0.85rem;
  color: var(--muted);
  background: #f0eee6;
  border-radius: 10px;
  padding: 10px 12px;
}

.terms-checkbox input[type="checkbox"] {
  width: 18px;
  height: 16px;
  accent-color: #2b2b2b;
  border-radius: 4px;
  border: 1.5px solid #2b2b2b;
  cursor: pointer;
}

.terms-checkbox a {
  color: #2b2b2b;
  text-decoration: underline;
  font-size: 0.95rem;
  font-weight: 600;
}

.account-links {
  margin-top: 18px;
}

.account-links p {
  text-align: center;
  font-size: 0.78rem;
  color: var(--muted);
}

.account-links a {
  color: var(--ink);
  font-weight: 700;
}

@media (max-width: 480px) {
  .signup-container {
    width: calc(100% - 32px);
    margin: 24px auto;
    padding: 22px 18px;
  }

  .Input_Group_S input {
    height: 44px;
    font-size: 14px;
  }
}

@media (max-width: 360px) {
  .signup-container {
    width: calc(100% - 24px);
    padding: 20px 16px;
  }
}
