.public-home {
  max-width: 1200px;
  background: #f7f6f2;
  color: #2b2b2b;
  border: 1px solid #e6e2d8;
  text-align: left;
  position: relative;
  overflow: hidden;
}

.public-home > * {
  position: relative;
  z-index: 1;
}

.public-home::before {
  content: "";
  position: absolute;
  width: 320px;
  height: 320px;
  right: -160px;
  top: -160px;
  background: linear-gradient(135deg, rgba(255, 170, 170, 0.25), rgba(255, 225, 225, 0));
  pointer-events: none;
}

.public-hero {
  margin-top: 12px;
}

.public-hero-grid {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  gap: 32px;
  align-items: center;
}

.public-title {
  font-size: 2.8rem;
  margin: 0 0 10px;
  letter-spacing: -1px;
  color: #2b2b2b;
}

.public-lead {
  font-size: 1.05rem;
  line-height: 1.7;
  color: #6f7379;
}

.public-eyebrow {
  font-size: 0.8rem;
  font-weight: 700;
  letter-spacing: 0.2em;
  text-transform: uppercase;
  color: #9b8f77;
  margin-bottom: 12px;
}

.public-hero-actions {
  display: flex;
  margin: 22px 0;
  margin-right: 350px;
}

.public-hero-actions button {
  border-radius: 12px;
  padding: 12px 20px;
  font-weight: 700;
  border: none;
}

.public-hero-actions .primary {
  background: #fee500;
  color: #2b2b2b;
}

.public-hero-actions .ghost {
  background: #ffffff;
  color: #2b2b2b;
  border: 1px solid #e6e2d8;
}

.public-pill-row {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.public-pill-row span {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 8px 14px;
  border-radius: 999px;
  background: #ece7dc;
  font-size: 0.82rem;
  color: #2b2b2b;
}

.public-hero-panel {
  background: #ffffff;
  border-radius: 20px;
  padding: 24px;
  box-shadow: 0 18px 32px rgba(43, 43, 43, 0.08);
  border: 1px solid #e6e2d8;
}

.panel-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 18px;
}

.panel-badge {
  background: #fee500;
  color: #2b2b2b;
  font-weight: 700;
  padding: 6px 12px;
  border-radius: 999px;
  font-size: 0.8rem;
}

.panel-sub {
  font-size: 0.85rem;
  color: #8b8f96;
}

.panel-body {
  display: grid;
  gap: 16px;
}

.panel-item {
  display: flex;
  gap: 12px;
  align-items: center;
}

.panel-item i {
  font-size: 1.4rem;
  color: #2b2b2b;
  background: #efeae0;
  padding: 10px;
  border-radius: 12px;
}

.panel-item p {
  margin: 2px 0 0;
  color: #8b8f96;
  font-size: 0.9rem;
}

.public-previews {
  margin: 24px 0;
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: 16px;
}

.public-quick {
  margin: 24px 0 10px;
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: 16px;
}

.public-quick-card {
  background: #ffffff;
  border-radius: 18px;
  padding: 18px;
  border: 1px solid #e6e2d8;
  box-shadow: 0 14px 24px rgba(43, 43, 43, 0.06);
  display: grid;
  gap: 8px;
}

.public-quick-card h3 {
  margin: 0;
  font-size: 1.1rem;
  color: #2b2b2b;
}

.public-quick-card p {
  margin: 0;
  color: #7a7f87;
  line-height: 1.6;
}

.public-quick-card a {
  color: #2b2b2b;
  font-weight: 700;
}

.preview-card {
  background: #ffffff;
  border-radius: 18px;
  padding: 18px;
  border: 1px solid #e6e2d8;
  box-shadow: 0 14px 24px rgba(43, 43, 43, 0.06);
}

.preview-header {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  font-weight: 700;
  color: #2b2b2b;
  margin-bottom: 12px;
}

.preview-header i {
  color: #2b2b2b;
}

.preview-card ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: grid;
  gap: 8px;
  color: #7a7f87;
  font-size: 0.95rem;
}

.public-features {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: 16px;
}
.public-feature-card {
  background: #ffffff;
  border-radius: 18px;
  padding: 20px;
  border: 1px solid #e6e2d8;
  box-shadow: 0 18px 28px rgba(43, 43, 43, 0.06);
}

.public-feature-card h3 {
  margin: 0 0 8px;
  font-size: 1.1rem;
  color: #2b2b2b;
}

.public-feature-card p {
  margin: 0;
  color: #7a7f87;
  line-height: 1.6;
}

.public-steps {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: 16px;
  margin-top: 26px;
}

.public-step-card {
  background: #ffffff;
  border-radius: 18px;
  padding: 18px;
  display: flex;
  gap: 12px;
  border: 1px solid #e6e2d8;
}

.public-step-card span {
  width: 34px;
  height: 34px;
  border-radius: 10px;
  background: #2b2b2b;
  color: #ffffff;
  font-weight: 800;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.public-step-card strong {
  display: block;
  color: #2b2b2b;
  margin-bottom: 4px;
}

.public-step-card p {
  margin: 0;
  color: #7a7f87;
}

.public-final {
  margin-top: 28px;
  background: #2b2b2b;
  color: #ffffff;
  border-radius: 18px;
  padding: 22px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 16px;
}

.public-final .section-title {
  color: #ffffff;
}

.public-final button.primary {
  background: #fee500;
  color: #2b2b2b;
}

.public-final button.primary:hover {
  background: #f6dd00;
}

@media (max-width: 900px) {
  .public-hero-grid {
    grid-template-columns: 1fr;
  }
  .public-quick,
  .public-previews,
  .public-features,
  .public-steps {
    grid-template-columns: 1fr;
  }
  .public-final {
    flex-direction: column;
    align-items: flex-start;
  }
}

@media (max-width: 600px) {
  .public-home {
    padding: 16px;
  }
  .public-title {
    font-size: 2.2rem;
  }
  .public-hero-actions {
    flex-direction: column;
    align-items: stretch;
    margin-right: 0px;
  }
  .public-home .nav-bar {
    display: none;
  }
  .public-home .nav-buttons {
    display: grid;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    gap: 8px;
  }
  .public-home .nav-buttons button {
    width: 100%;
    justify-content: center;
    padding: 10px 12px;
    font-size: 0.95rem;
  }
}
