.admin-dashboard {
  width: min(1100px, 94vw);
  margin: 28px auto;
  padding: 24px;
  background: #fff;
  border-radius: 16px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.08);
}

.admin-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 16px;
  margin-bottom: 20px;
}

.admin-header h2 {
  margin: 0 0 6px 0;
  font-size: 1.8rem;
}

.admin-header p {
  margin: 0;
  color: #6c757d;
}

.admin-summary {
  display: flex;
  gap: 12px;
}

.summary-card {
  background: #f8f9fc;
  border: 1px solid #e9ecef;
  border-radius: 12px;
  padding: 12px 16px;
  min-width: 120px;
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.summary-card strong {
  font-size: 1.4rem;
}

.admin-tabs {
  display: inline-flex;
  gap: 8px;
  margin-bottom: 18px;
  border-bottom: 1px solid #e9ecef;
  padding-bottom: 8px;
}

.admin-tabs button {
  border: none;
  background: transparent;
  font-size: 1rem;
  font-weight: 600;
  padding: 6px 12px;
  cursor: pointer;
  color: #6c757d;
}

.admin-tabs button.active {
  color: #0b5ed7;
  border-bottom: 2px solid #0b5ed7;
}

.admin-panel {
  background: #f8f9fc;
  border-radius: 14px;
  padding: 18px;
  border: 1px solid #e9ecef;
}

.admin-toolbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 12px;
  margin-bottom: 16px;
}

.filter-group {
  display: flex;
  align-items: center;
  gap: 10px;
  font-weight: 600;
}

.filter-group select {
  border: 1px solid #ced4da;
  border-radius: 10px;
  padding: 6px 10px;
  font-size: 0.95rem;
}

.admin-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: grid;
  gap: 12px;
}

.admin-item {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 16px;
  padding: 16px;
  border-radius: 12px;
  background: #fff;
  border: 1px solid #e9ecef;
}

.admin-item-main {
  display: grid;
  gap: 6px;
}

.admin-item-title {
  display: flex;
  align-items: center;
  gap: 10px;
}

.admin-item-target,
.admin-item-reason {
  margin: 0;
  color: #495057;
}

.admin-item-meta {
  display: flex;
  gap: 16px;
  flex-wrap: wrap;
  font-size: 0.85rem;
  color: #6c757d;
}

.admin-sub {
  display: block;
  color: #6c757d;
  font-size: 0.9rem;
}

.admin-item-actions {
  display: flex;
  gap: 8px;
}

.status-badge {
  font-size: 0.8rem;
  padding: 4px 8px;
  border-radius: 999px;
  background: #ffe9e7;
  color: #b3261e;
}

.status-badge.resolved {
  background: #e8f5ea;
  color: #1f6b35;
}

.severity-badge {
  font-size: 0.8rem;
  padding: 4px 8px;
  border-radius: 999px;
  background: #eef2ff;
  color: #2f3f9e;
}

.severity-badge.high {
  background: #ffe5e5;
  color: #b42318;
}

.severity-badge.medium {
  background: #fff4e5;
  color: #b25e09;
}

.severity-badge.low {
  background: #e8f5ea;
  color: #1f6b35;
}

.admin-link {
  color: #0b5ed7;
  margin-left: 8px;
  text-decoration: none;
}

.admin-link:hover {
  text-decoration: underline;
}

.admin-empty {
  color: #6c757d;
  font-size: 0.95rem;
}

.btn-primary,
.btn-light,
.btn-danger {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  border-radius: 10px;
  padding: 8px 12px;
  border: none;
  cursor: pointer;
}

.btn-primary {
  background: #0b5ed7;
  color: #fff;
}

.btn-light {
  background: #f1f3f5;
  color: #343a40;
}

.btn-danger {
  background: #dc3545;
  color: #fff;
}

.admin-modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(15, 23, 42, 0.45);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 16px;
  z-index: 80;
}

.admin-modal {
  width: min(420px, 100%);
  background: #fff;
  border-radius: 14px;
  padding: 20px;
  display: grid;
  gap: 12px;
}

.admin-modal input {
  border: 1px solid #ced4da;
  border-radius: 10px;
  padding: 10px 12px;
}

.admin-modal-actions {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
}

@media (max-width: 720px) {
  .admin-dashboard {
    padding: 18px;
  }

  .admin-header {
    flex-direction: column;
    align-items: flex-start;
  }

  .admin-summary {
    width: 100%;
  }

  .summary-card {
    flex: 1;
  }

  .admin-toolbar {
    flex-direction: column;
    align-items: flex-start;
  }

  .admin-item {
    flex-direction: column;
    align-items: stretch;
  }

  .admin-item-actions {
    justify-content: flex-end;
    flex-wrap: wrap;
  }
}
