.find-page {
  min-height: 100vh;
  background: #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 32px 18px 48px;
  font-family: "Pretendard", "NanumSquareNeo", "Segoe UI", "Apple SD Gothic Neo", sans-serif;
  color: #0f1b2d;
}

.find-card {
  width: min(540px, 100%);
  padding: 22px 24px 30px;
  border: none;
  border-radius: 0;
  background: #ffffff;
  display: flex;
  flex-direction: column;
  gap: 12px;
  box-shadow: none;
}

.find-title {
  margin: 0;
  font-size: 1.8rem;
  font-weight: 700;
  text-align: center;
}

.find-form {
  display: flex;
  flex-direction: column;
  gap: 18px;
}

.find-form input:focus {
  outline: none;
  border: none;
  box-shadow: none;
}

.find-label {
  font-weight: 600;
  color: #111111;
}

.find-input {
  display: flex;
  align-items: center;
  border: 2px solid #111111;
  border-style: solid;
  border-radius: 4px;
  padding: 5px 4px;
  height: 100%;
  gap: 8px;
  background: #ffffff;
}

.find-field {
  width: 100%;
  border: none;
  background: transparent;
  padding: 8px 2px;
  font-size: 1rem;
  color: #0f1b2d;
}

.find-input-icon {
  color: #111111;
  font-size: 1.2rem;
}

.find-action {
  border: none;
  border-radius: 2px;
  padding: 14px 16px;
  font-weight: 700;
  font-size: 1rem;
  color: #ffffff;
  background: #111111;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  cursor: pointer;
  transition: transform 0.2s ease, opacity 0.2s ease;
}

.find-action:disabled {
  cursor: not-allowed;
  opacity: 0.6;
  transform: none;
}

.find-action:not(:disabled):hover {
  transform: translateY(-1px);
}

.find-annot {
  position: relative;
  height: 48px;
}

.find-annot-arrow {
  position: absolute;
  left: 0;
  top: -6px;
  width: 24px;
  height: 40px;
  border-left: 3px solid #111111;
}

.find-annot-arrow::after {
  content: "";
  position: absolute;
  top: 0;
  left: -8px;
  width: 0;
  height: 0;
  border-top: 6px solid transparent;
  border-bottom: 6px solid transparent;
  border-left: 10px solid #111111;
}

.find-hint {
  margin: 0;
  font-size: 0.95rem;
  color: #111111;
}

.find-note {
  display: flex;
  align-items: flex-start;
  gap: 10px;
  padding: 12px 14px;
  border-radius: 10px;
  font-size: 0.95rem;
  line-height: 1.4;
}

.find-note.success {
  background: #e8f5ea;
  color: #1f6b35;
}

.find-note.error {
  background: #ffe9e7;
  color: #b3261e;
}

.spin {
  animation: spin 1s linear infinite;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media (max-width: 600px) {
  .find-page {
    padding: 24px 16px 36px;
  }

  .find-card {
    padding: 22px 16px 24px;
  }

  .find-title {
    font-size: 1.6rem;
  }

  .find-annot {
    height: 36px;
  }

  .find-annot-arrow {
    height: 30px;
  }
}
