.auth-container {
  --ink: #2b2b2b;
  --muted: #8b8f96;
  --accent: #fee500;
  --surface: #f7f6f2;
  --field: #ffffff;
  --edge: #e6e2d8;
  width: min(380px, 100%);
  margin: 36px auto;
  padding: 28px 24px 24px;
  box-sizing: border-box;
  border-radius: 18px;
  background: var(--surface);
  border: 1px solid var(--edge);
  position: relative;
  overflow: hidden;
}

.auth-container::before {
  content: "";
  position: absolute;
  width: 240px;
  height: 240px;
  right: -140px;
  top: -140px;
  background: linear-gradient(135deg, rgba(255, 170, 170, 0.35), rgba(255, 225, 225, 0));
  pointer-events: none;
}

.auth-hero {
  margin-top: 8px;
  margin-bottom: 22px;
}

.auth-hero h2 {
  text-align: left;
  font-size: 1.55rem;
  font-weight: 800;
  color: var(--ink);
  margin: 0 0 12px;
  letter-spacing: -0.02em;
  line-height: 1.35;
}

.auth-sub {
  margin: 0;
  font-size: 0.84rem;
  color: var(--muted);
  line-height: 1.5;
}

.social-stack {
  display: grid;
  gap: 10px;
  margin-bottom: 12px;
}

.social-login {
  display: flex;
  align-items: center;
  gap: 12px;
  width: 100%;
  border-radius: 12px;
  padding: 12px 14px;
  border: 1px solid #e7e7e7;
  background: #ffffff;
  cursor: pointer;
  transition: transform 0.2s ease, filter 0.2s ease;
}

.social-login:hover {
  transform: translateY(-1px);
  filter: brightness(0.99);
}

.social-login img {
  width: 34px;
  height: 34px;
  object-fit: contain;
}

.social-login.naver {
  background: #03c75a;
  border-color: #03c75a;
}

.social-login.naver .social-title,
.social-login.naver .social-sub {
  color: #ffffff;
}

.social-texts {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.social-title {
  font-size: 0.95rem;
  font-weight: 700;
  color: var(--ink);
}

.social-sub {
  font-size: 0.78rem;
  color: var(--muted);
}

.other-login-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 44px;
  border: none;
  border-radius: 12px;
  background: #e9e8e4;
  color: #6f7379;
  font-size: 0.92rem;
  font-weight: 700;
  cursor: pointer;
  transition: filter 0.2s ease;
  text-decoration: none;
}

.other-login-button:hover {
  filter: brightness(0.98);
}

.email-form {
  margin-top: 16px;
}

.Input_Group {
  position: relative;
  margin-bottom: 16px;
  width: 100%;
}

.Input_Group input {
  width: 100%;
  height: 46px;
  padding: 12px 12px 12px 44px;
  border: 1px solid var(--edge);
  border-radius: 12px;
  font-size: 15px;
  color: var(--ink);
  background: var(--field);
  transition: border-color 0.2s ease;
  outline: none;
  box-sizing: border-box;
}

.Input_Group input:focus {
  border-color: #2b2b2b;
}

.Input_Group i {
  position: absolute;
  top: 44%;
  left: 14px;
  transform: translateY(-50%);
  font-size: 24px;
  color: #a6aab1;
  pointer-events: none;
  transition: color 0.2s ease;
}

.Input_Group input:focus ~ i {
  color: #2b2b2b;
}

.Input_Group label {
  position: absolute;
  left: 44px;
  top: 45%;
  transform: translateY(-50%);
  color: #a0a4aa;
  font-size: 20px;
  padding: 0 6px;
  background: transparent;
  transition: all 0.2s ease;
  pointer-events: none;
}

.Input_Group input:focus + label,
.Input_Group input:not(:placeholder-shown) + label {
  top: -8px;
  left: 38px;
  font-size: 11px;
  color: #2b2b2b;
  background: var(--surface);
  font-weight: 600;
}

.email-form button[type="submit"] {
  width: 100%;
  height: 48px;
  border: none;
  border-radius: 12px;
  color: #ffffff;
  font-size: 1rem;
  font-weight: 700;
  cursor: pointer;
  transition: transform 0.2s ease, filter 0.2s ease;
}

.email-form button[type="submit"]:hover {
  transform: translateY(-1px);
  filter: brightness(0.98);
}

.auth-container .account-links {
  margin-top: 18px;
}

.auth-container .account-links p {
  text-align: center;
  font-size: 0.78rem;
  color: var(--muted);
}

.auth-container .account-links a {
  color: var(--ink);
  font-weight: 700;
}

@media (max-width: 480px) {
  .auth-container {
    width: calc(100% - 32px);
    margin: 24px auto;
    padding: 22px 18px;
  }

  .Input_Group input {
    height: 44px;
    font-size: 14px;
  }
}

@media (max-width: 360px) {
  .auth-container {
    width: calc(100% - 24px);
    padding: 20px 16px;
  }
}
