body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.grade-modal-backdrop {
  position: fixed;
  inset: 0;
  background: rgba(10, 20, 40, 0.55);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  z-index: 9998;
}

.grade-modal {
  width: min(420px, 100%);
  background: #ffffff;
  border-radius: 14px;
  padding: 20px;
  box-shadow: 0 18px 40px rgba(8, 18, 40, 0.2);
  text-align: left;
}

.grade-modal h3 {
  margin: 0 0 8px;
  text-align: left;
}

.grade-modal-desc {
  margin: 0 0 16px;
  color: #555;
  font-size: 0.95rem;
}

.grade-modal-form {
  display: grid;
  gap: 14px;
}

.grade-fields {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  gap: 12px;
}

.grade-field {
  display: grid;
  gap: 6px;
  font-weight: bold;
  color: #333;
}

.grade-field input {
  margin: 0;
}

.grade-modal-actions {
  display: flex;
  gap: 10px;
}

.grade-modal-actions button {
  flex: 1;
}

.grade-modal-hint {
  margin: 8px 0 0;
  font-size: 0.85rem;
  color: #777;
}

@media (max-width: 600px) {
  .grade-fields {
    grid-template-columns: 1fr;
  }
}
