.mobile-bottom-nav {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  display: none;
  justify-content: space-around;
  align-items: center;
  gap: 4px;
  height: 62px;
  padding: 6px 10px;
  background: #ffffff;
  border-top: 1px solid #e6e9f0;
  box-shadow: 0 -8px 20px rgba(15, 23, 42, 0.08);
  z-index: 80;
}

.mobile-bottom-nav-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 4px;
  min-width: 56px;
  color: #6c757d;
  font-size: 0.75rem;
  text-decoration: none;
}

.mobile-bottom-nav-item i {
  font-size: 1.25rem;
}

.mobile-bottom-nav-item.active {
  color: #0d6efd;
  font-weight: 600;
}

@media (max-width: 768px) {
  body {
    padding-bottom: 72px;
  }

  .mobile-bottom-nav {
    display: flex;
  }
}
